order-box;\n  top: 0;\n  bottom: 0;\n}\n\n.wrapContainerLamp {\n  position: relative;\n\n  --color-r: 0;\n  --color-g: 0;\n  --color-b: 0;\n\n  // reuse colors\n  &Default {\n    --color-r: 225;\n    --color-g: 225;\n    --color-b: 225;\n  }\n\n  &Red {\n    --color-r: 255;\n    --color-g: 0;\n    --color-b: 0;\n  }\n\n  &Green {\n    --color-r: 54;\n    --color-g: 214;\n    --color-b: 174;\n  }\n\n  &Blue {\n    --color-r: 31;\n    --color-g: 203;\n    --color-b: 255;\n  }\n\n  &Yellow {\n    --color-r: 255;\n    --color-g: 217;\n    --color-b: 0;\n  }\n\n  &Pink {\n    --color-r: 246;\n    --color-g: 43;\n    --color-b: 253;\n  }\n}\n\n.wrapContainerLamp {\n  &::before {\n    left: 0;\n  }\n\n  &::after {\n    right: 0;\n  }\n\n  &::before,\n  &::after {\n    @include ion-lamp;\n  }\n}\n\n.containerGradientText {\n  width: 100%;\n  min-height: 50px;\n  position: relative;\n  padding: 0px 5px;\n  height: 100%;\n\n  &::after,\n  &::before {\n    @include styleGradientPseudoEl;\n  }\n\n  &::after {\n    @include linear-gradient(90deg);\n    right: 0;\n  }\n\n  &::before {\n    left: 0;\n    @include linear-gradient(-90deg);\n  }\n\n  &Content {\n    position: relative;\n    margin: 0px -5px;\n    padding: 15px;\n    overflow-x: auto;\n    overflow-y: hidden;\n  }\n}\n\n.togglingDisable {\n  .containerContainerGradientTitle {\n    cursor: default;\n  }\n\n  .containerContainerGradientContent {\n    height: auto;\n  }\n}\n\n.titleTogglingActive {\n  cursor: pointer;\n}\n\n// TODO: remove\n.containerTxs {\n  width: 100%;\n  padding: 15px;\n  display: flex;\n  width: 100%;\n  flex-direction: column;\n  gap: 15px;\n\n  &TxHash {\n    display: flex;\n    width: 100%;\n    align-items: center;\n    justify-content: space-between;\n    position: relative;\n    z-index: 1;\n  }\n\n  &RawLog {\n    position: relative;\n    z-index: 1;\n  }\n\n  &Confirmed {\n    position: relative;\n\n    &::before {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      left: 0;\n      opacity: 11%;\n    }\n\n    &::after {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(90deg);\n      right: 0;\n      opacity: 11%;\n    }\n  }\n\n  &Pending {\n    position: relative;\n\n    &::before {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      left: 0;\n      opacity: 12%;\n    }\n\n    &::after {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(90deg);\n      right: 0;\n      opacity: 12%;\n    }\n  }\n\n  &Danger {\n    position: relative;\n\n    &::after {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(90deg);\n      right: 0;\n      opacity: 11%;\n    }\n\n    &::before {\n      @include styleGradientPseudoEl;\n      @include linear-gradient(-90deg);\n      left: 0;\n      opacity: 11%;\n    }\n  }\n}\n","@mixin saber-background($position) {\n  content: '';\n  position: absolute;\n  z-index: -2;\n  #{$position}: 0;\n\n  @if ($position == left or $position == right) {\n    top: 0;\n    height: 100%;\n    width: 20%;\n  } @else {\n    width: 100%;\n    left: 0;\n    height: 50px;\n  }\n\n  filter: blur(1px);\n\n  background: linear-gradient(\n    /* vise versa */ to #{$position},\n    rgb(0, 0, 0) 0%,\n    rgba(var(--color-r), var(--color-g), var(--color-b), 0.11) 90%,\n    rgba(var(--color-r), var(--color-g), var(--color-b), 0.15) 95%,\n    rgba(var(--color-r), var(--color-g), var(--color-b), 0.1) 100%\n  );\n}\n\n// check Storybook for more documentation\n$valid-colors: 'green', 'blue', 'purple', 'white', 'red', 'yellow', 'orange';\n$valid-positions: 'left', 'right', 'top', 'bottom';\n$saber-border-width: 2px;\n\n@mixin saber($color: 'green', $position, $borderColor: 'white') {\n  position: relative;\n\n  // box-shadow: 0 0 10px rgba(var(--color-r), var(--color-g), var(--color-b), 0.5);\n\n  @if ($color == 'green') {\n    --color-r: 54;\n    --color-g: 214;\n    --color-b: 174;\n  } @else if ($color == 'blue') {\n    --color-r: 31;\n    --color-g: 203;\n    --color-b: 255;\n  } @else if ($color == 'purple') {\n    --color-r: 246;\n    --color-g: 43;\n    --color-b: 253;\n  } @else if ($color == 'white') {\n    --color-r: 225;\n    --color-g: 225;\n    --color-b: 225;\n  } @else if ($color == 'red') {\n    --color-r: 255;\n    --color-g: 0;\n    --color-b: 0;\n  } @else if ($color == 'yellow') {\n    --color-r: 255;\n    --color-g: 217;\n    --color-b: 0;\n  } @else if ($color == 'orange') {\n    --color-r: 255;\n    --color-g: 92;\n    --color-b: 0;\n  } @else {\n    @error \"Invalid color: #{$color}\";\n  }\n\n  @if ($borderColor == 'color') {\n    border-#{$position}: $saber-border-width\n      solid\n      rgb(var(--color-r), var(--color-g), var(--color-b));\n  } @else {\n    border-#{$position}: $saber-border-width solid white;\n  }\n\n  @if ($position == left or $position == top) {\n    &::before {\n      @include saber-background($position);\n    }\n  } @else if ($position == right or $position == bottom) {\n    &::after {\n      @include saber-background($position);\n    }\n  } @else {\n    @error \"Invalid position: #{$position}\";\n  }\n}\n\n@mixin saber-horizontal($color, $borderColor: 'white') {\n  @include saber($color, left, $borderColor);\n  @include saber($color, right, $borderColor);\n}\n\n@mixin saber-vertical($color) {\n  @include saber($color, top);\n  @include saber($color, bottom);\n}\n"],"sourceRoot":""}]);
