tems: center;\n  height: 100px;\n  border: none;\n  background: transparent;\n  color: rgb(31, 203, 255);\n  margin-top: -10px;\n  cursor: pointer;\n\n  &:hover {\n    .networkBtn {\n      &::before {\n        width: 105px;\n        height: 105px;\n      }\n    }\n\n    .networkBtnImg {\n      width: 65px;\n      height: 65px;\n    }\n\n    .containerBtnItemSelectText {\n      transform: translateX(5px);\n    }\n  }\n}\n\n.networkBtn {\n  border: none;\n  background: transparent;\n  position: relative;\n  cursor: pointer;\n\n  &::before {\n    content: '';\n    width: 100px;\n    height: 100px;\n    background: #000000;\n    box-shadow: 0px 0px 20px rgba(31, 203, 255, 0.4);\n    position: absolute;\n    border-radius: 50%;\n    // z-index: -1;\n    display: flex;\n    top: 50%;\n    transform: translate(-50%, -50%);\n    left: 50%;\n    transition: 0.2s;\n  }\n}\n\n.networkBtnImg {\n  width: 60px;\n  height: 60px;\n  position: relative;\n}\n\n.networkBtnImgIconMenu {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transition: 0.2s;\n  transform: translate(-50%, -50%) rotate(-90deg);\n\n  &Close {\n    transform: translate(-50%, -50%) rotate(0deg);\n  }\n\n  div {\n    width: 35px;\n    height: 5px;\n    background-color: black;\n    margin: 6px 0;\n  }\n}\n\n.btnContainerText {\n  border: none;\n  font-size: 16px;\n  background: transparent;\n  color: rgb(31, 203, 255);\n  cursor: pointer;\n}\n\n.btnContainerTextHover {\n  display: flex;\n  justify-content: flex-end;\n  font-size: 16px;\n}\n\n.containerInfoSwitch {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-end;\n  justify-content: center;\n  height: 100%;\n  gap: 10px;\n  color: var(--blue-light);\n}\n\n.containerInfoSwitchGapUnSet {\n  gap: unset;\n}\n\n.containerBandwidthBar {\n  width: 100%;\n}\n\n.containerSwichNetworkList {\n  display: flex;\n  flex-direction: column;\n  background: #0000008c;\n  padding-left: 15px;\n  width: 250px;\n  padding-bottom: 15px;\n  opacity: 0;\n  transit