: 10px;\n  height: 95px;\n\n  @media (max-width: 768px) {\n    grid-template-columns: 90px 1fr;\n  }\n\n  &Img {\n    width: 70px;\n    height: 70px;\n    object-fit: contain;\n    transition: 0.2s;\n\n    @media (max-width: 768px) {\n      width: 90px;\n      height: 90px;\n    }\n  }\n\n  &Title {\n    font-size: 16px;\n    color: #36d6ae;\n    margin: 8px 0;\n\n    @media (max-width: 768px) {\n      font-size: 20px;\n    }\n  }\n\n  &Dsc {\n    font-size: 14px;\n    color: #777777;\n    @media (max-width: 768px) {\n      font-size: 18px;\n    }\n  }\n\n  &:hover {\n    cursor: pointer;\n\n    .containerItemPlayContentImg {\n      width: 75px;\n      height: 75px;\n      object-fit: contain;\n      @media (max-width: 768px) {\n        width: 95px;\n        height: 95px;\n      }\n    }\n\n    .containerPlayContentContainerText {\n      transform: translateX(5px);\n    }\n  }\n}\n\n.containteImgItemPlayContent {\n  display: flex;\n  justify-content: center;\n}\n"],"sourceRoot":""}]);
